import HomeIcon from '@mui/icons-material/Home'
import BadgeIcon from '@mui/icons-material/Badge'
import Supplies from './supplies/Supplies'

function Home() {
   return (
      <div>
         <h1> </h1>
      </div>
   )
}

function NotFound() {
   return (
      <div>
         <h1>404</h1>
      </div>
   )
}

const pages  = [
   {
      name: '',
      path: '/',
      icon: HomeIcon,
      component: Home,
   },
   {
      name: '',
      path: '/supplies',
      icon: BadgeIcon,
      component: Supplies,
   },
   {
      name: ' ',
      path: '*',
      icon: null,
      component: NotFound,
   },
]


export default pages;